.dialogs {
    display: flex;
    width: 100%;
    border-radius: 13px;
    height: 85vh;
    padding-bottom: 35px;
}

.chatWrapper{
    border-radius: 13px;
    height: 100%;
    width: 77%;
    background-color: #A679CA;
    background-color: rgb(255, 255, 255);
    box-shadow: 1px 2px 10px 2px #7f5d9c;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: auto;
} .chat {
    overflow: auto;
    max-width: 100%;
} .messagesField {
    word-wrap: break-word;
    padding: 15px;
    overflow-x:hidden;
    overflow-y:auto;
    
} .message{
    padding: 5px;
} 

.addMessageField {
     display: flex;
}

.chatWrapper textarea {
    border-bottom-left-radius: 13px;
    height: 50px;
    resize: none;
    color: rgb(0, 0, 0);
    width: 80%;
    border: 1px solid rgb(0, 0, 0);
}

.chatWrapper textarea::placeholder {
    color: #000000;
}

.chatWrapper button {
    border-bottom-right-radius: 13px;
    background-color: #A679CA;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    width: 20%;
    cursor: pointer;
    border: 1px solid rgb(0, 0, 0);
    
}
.chatWrapper button:hover{
    background-color: #efefef;
}

.dialogsItems {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 13px;
    width: 20%;
    margin-left: 10px;
    text-align: center;
    text-decoration: none;
    font-weight: 400;


    background-color: #A679CA;
    background-color: rgb(255, 255, 255);
    box-shadow: 1px 2px 10px 2px #7f5d9c;
}


@media (max-width: 560px) {
    .dialogs {
        flex-direction: column-reverse;
        height: 95vh;
        max-height: 95vh;
        padding: 0;
        margin: 0;
    }
    .dialogsItems{
        border-radius: 0;
        border-top-right-radius: 0;
        width: 100%;
        margin: 0;
        padding: 0;
        margin-bottom: 10px;
        flex-direction: row;
    }
    .chatWrapper{
        width: 100%;
        border-radius: 0;
    }
    .chatWrapper textarea {
        border-bottom-left-radius: 0px;
    }
    
    .chatWrapper button {
        border-bottom-right-radius: 0;
    }
}
  